import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { SupportChartData1} from './chart/support-chart-data-1';
import { SupportChartData2} from './chart/support-chart-data-2';
import { SupportChartData3} from './chart/support-chart-data-3';
import { SupportChartData4 } from './chart/support-chart-data-4'

@Component({
  selector: 'app-data-source',
  templateUrl: './data-source.component.html',
  styleUrls: ['./data-source.component.scss']
})
export class DataSourceComponent implements OnInit {
  dtColumnsReorderOptionsQPay: any = {};
  dtColumnsReorderOptionsQString: any = {};
  dtColumnsReorderOptionsSAP: any = {};
  currentTable: String = "QPay";
  public supportChartData1: any;
  public supportChartData2: any;
  public supportChartData3: any;
  public supportChartData4: any;

  constructor(private router: Router,private route: ActivatedRoute) {
    this.supportChartData1 = SupportChartData1.supportChartData;
    this.supportChartData2 = SupportChartData2.supportChartData;
    this.supportChartData3 = SupportChartData3.supportChartData;
    this.supportChartData4 = SupportChartData4.supportChartData;
  }

  switchDataTable(type) {
    this.currentTable = type;
  }
  navigate(path) {
    this.router.navigate([path]);
  }
  ngOnInit() {
    this.dtColumnsReorderOptionsQPay = {
      ajax: 'fake-data/datatable-qpay.json',
      columns: [{
        title: 'Table name',
        data: 'TableName'
      }, {
        title: 'Number of attributes',
        data: 'ColumnCount'
      }, {
        title: 'Number of Records',
        data: 'RowCount'
      }],
      //dom: 'Rt',
      paging: true,
      pagingType: "full_numbers",
      colReorder: {
        order: [0, 2, 1],
        fixedColumnsRight: 1
      },
      responsive: true
    };

    this.dtColumnsReorderOptionsQString = {
      ajax: 'fake-data/datatable-qstring.json',
      columns: [{
        title: 'Table name',
        data: 'TableName'
      }, {
        title: 'Number of attributes',
        data: 'ColumnCount'
      }, {
        title: 'Number of Records',
        data: 'RowCount'
      }],
      //dom: 'Rt',
      paging: true,
      pagingType: "full_numbers",
      colReorder: {
        order: [0, 2, 1],
        fixedColumnsRight: 1
      },
      responsive: true
    };

    this.dtColumnsReorderOptionsSAP = {
      ajax: 'fake-data/datatable-sap.json',
      columns: [{
        title: 'Table name',
        data: 'TableName'
      }, {
        title: 'Number of attributes',
        data: 'ColumnCount'
      }, {
        title: 'Number of Records',
        data: 'RowCount'
      }],
      //dom: 'Rt',
      paging: true,
      pagingType: "full_numbers",
      colReorder: {
        order: [0, 2, 1],
        fixedColumnsRight: 1
      },
      responsive: true
    };
    this.currentTable = 'QPay';
  }

}
