    this.streamId = streamId;
    switch(this.streamId){
      case 1:
        this.areaChartData1 = AreaChartData.areaChart1;
        this.barChartData1 = BarChartData.barChart1;
        break;
      case 2:
        this.areaChartData1 = AreaChartData.areaChart2;
        this.barChartData1 = BarChartData.barChart2;
        break;
      case 3:
        this.areaChartData1 = AreaChartData.areaChart3;
        this.barChartData1 = BarChartData.barChart3;
        break;
      default:
        this.streamId = 0;
    }
