import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AdminComponent } from './theme/layout/admin/admin.component';
import { DataSourceComponent } from './data-source/data-source.component';
import { LoginComponent } from './login/login.component';
import { DataStreamersComponent } from './data-streamers/data-streamers.component';
import { DataMartComponent } from './data-mart/data-mart.component';
import { MicroservicesComponent } from './microservices/microservices.component';
import {AuthComponent} from './theme/layout/auth/auth.component';

const routes: Routes = [
  {
    path: '',
    component: AdminComponent,
    children: [
      {
        path: '',
        redirectTo: 'login',
        pathMatch: 'full'
      },
      {
        path: 'data-source',
        component: DataSourceComponent
      },
      {
        path: 'login',
        component: LoginComponent
      },
      {
        path: 'data-streamers',
        component: DataStreamersComponent
      },
      {
        path: 'data-mart',
        component: DataMartComponent
      },
      {
        path: 'microservices',
        component: MicroservicesComponent
      },
      {
        path: '',
        redirectTo: 'data-source',
        pathMatch: 'full'
      },
      {
        path: 'sample-page',
        loadChildren: () => import('./demo/pages/sample-page/sample-page.module').then(module => module.SamplePageModule)
      }
    ]
  },
  {
    path: '',
    component: AuthComponent,
    children: []
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
