    // this.formattedEndDate = this.toDate.year+'-'+ (this.toDate.month < 10 ? '0'+this.toDate.month: this.toDate.month) +'-'+ (this.toDate.day < 10 ? '0'+this.toDate.day: this.toDate.day);
