import {Injectable} from '@angular/core';

export interface NavigationItem {
  id: string;
  title: string;
  type: 'item' | 'collapse' | 'group';
  translate?: string;
  icon?: string;
  hidden?: boolean;
  url?: string;
  classes?: string;
  exactMatch?: boolean;
  external?: boolean;
  target?: boolean;
  breadcrumbs?: boolean;
  function?: any;
  badge?: {
    title?: string;
    type?: string;
  };
  children?: Navigation[];
}

export interface Navigation extends NavigationItem {
  children?: NavigationItem[];
}

const NavigationItems = [
  {
    type: 'group',
    icon: 'feather icon-align-left',
    children: [
      {
        id: 'dataSource',
        title: 'Data Sources',
        type: 'item',
        url: '/data-source',
        classes: 'nav-item',
        icon: 'feather icon-sidebar'
      },
      {
        id: 'dataStreamersTransformers',
        title: 'Streamers & Transformers',
        url: '/data-streamers',
        type: 'item',
        icon: 'feather icon-cloud-off',
        classes: 'nav-item',
      },
      {
        id: 'dataMart',
        title: 'Data Mart',
        url: '/data-mart',
        type: 'item',
        icon: 'feather icon-droplet',
        classes: 'nav-item',
      },
      {
        id: 'microServices',
        title: 'Microservices',
        url: '/microservices',
        type: 'item',
        icon: 'feather icon-file',
        classes: 'nav-item',
      }
    ]
  }
];

@Injectable()
export class NavigationItem {
  public get() {
    return NavigationItems;
  }
}
